
.names {
  text-align: center;
  font-family: 'lust-script-display', sans-serif;
  font-weight: normal;
  font-size: 3rem;
  line-height: 1.2;
  margin: 0 auto 1rem;
  max-width: 6em;
}
@media screen and (min-width: 32rem) {
.names {
    text-align: right;
    font-size: 4rem;
    max-width: 6em;
}
}
.link {
  text-decoration: none;
  color: inherit;
}
.nameAnd {
  font-size: 0.6em;
  line-height: 1;
  margin: -0.2em 0;
  display: block;
}
@media screen and (min-width: 32rem) {
.name1 {
    display: block;
    text-align: left;
}
.nameAnd {
    font-size: 0.6em;
    vertical-align: middle;
    display: inline-block;
    margin: 0;
}
}


